@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@100;500;700&display=swap);
:root {
  --bg-color:#212529;
  --main-color: #49A0EB;
  --second-color: #E359A4;
}

body {
  background-color: #212529;
  background-color: var(--bg-color);
  color: #E359A4;
  color: var(--second-color);
  font-family: 'IBM Plex Mono', monospace;
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
}

.mobile-only {
  display: block;
}

.no-mobile {
  display: none;
}

h1 {
  color: #49A0EB;
  color: var(--main-color);
}

button {
  background-color: #212529;
  background-color: var(--bg-color);
  border-radius: 5px;
  border: 1px dashed #E359A4;
  border: 1px dashed var(--second-color);
  color: #E359A4;
  color: var(--second-color);
  font-family: 'IBM Plex Mono', monospace;
  padding: .5em 2em;
  width: 120px;
}

button:hover {
  cursor: pointer;
  border: 1px solid #E359A4;
  border: 1px solid var(--second-color);
}

button.active {
  background-color: #E359A4;
  background-color: var(--second-color);
  color: #212529;
  color: var(--bg-color);
  border: 1px dashed #212529;
  border: 1px dashed var(--bg-color);
  box-sizing: border-box;
}

header h1 {
  color: #49A0EB;
  color: var(--main-color);
  font-weight: 800;
}

header {
  display: grid;
  grid-template-columns: 100px 1fr;
  width: 100%;
}

.intro-box {
  -webkit-align-items: center;
          align-items: center;
  background-color: #49A0EB;
  background-color: var(--main-color);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.intro-box img {
  height: 100px;
}

.bio {
  padding: 1em;
}

p.bio {
  grid-column: 1 / 3;
  padding: 2em;
}

p.meta {
  color: #49A0EB;
  color: var(--main-color);
  font-size: .85em;
  font-weight: bold;
  margin: 0;
}

p.meta-blue {
  color: #49A0EB;
  color: var(--main-color);
  font-size: .85em;
  font-style: italic;
  font-weight: bold;
  margin: 0;
}

i {
  color: #49A0EB;
  color: var(--main-color);
}

i.meta-blue {
  color: #E359A4;
  color: var(--second-color);
}

.heading h1 {
  font-size: 2em;
  padding-left: 1em;
}

.x-scroll{
  overflow-x: scroll;
}

.container {
  padding-left: 4em;
}

.margin-left {
  margin-left: 2em;
}

.top-padding-small {
  padding-top: 2em;
}

.flex-container {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-left: 2em;
  width: 80%;
}

.flex-container button {
  margin-right: 2em;
  margin-bottom: 2em;
}

.column-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.chart-container {
  height: 400px;
}

.chart-inner {
  min-width: 1000px;
  overflow-y: hidden;
}

.large-list p {
  color: #E359A4;
  color: var(--second-color);
  font-size: 1em;
  margin: 0;
  padding: 0;
}

.company-name-container {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
}

.company-name-container > p.meta {
  margin-left: 1em;
}

ul.bulleted-list {
  list-style: circle;
  padding-left: 2em;
  padding-top: .75em;
}

p.role {
  color: #49A0EB;
  color: var(--main-color);
  padding-right: 2em;
  max-width: 500px;
}

.company-list {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0 2em;
}

.company-list > li {
  width: 100%;
  padding-bottom: 2em;
}

.middle-info {
  margin: 0 auto;
  padding: 2em 0;
  width: 100%;
}


@media (min-width: 768px) {
  .mobile-only {
    display: none;
  }

  .no-mobile {
    display: block;
  }

  header {
    grid-template-columns: 2fr 2fr;
    padding-top: 5em;
  }

  header h1 {
    font-size: 3em;
  }

  .margin-left {
    margin-left: 4em;
  }

  .intro-box {
    grid-row: 0 / 2;
  }

  .intro-box img {
    height: 260px;
  }

  .bio {
    padding: 0 3em 0 3em;
    width: 75%;
  }

  .bio p {
    line-height: 1.5em;
  }
  .heading h1 {
    font-size: 2em;
    padding-left: 0;
  }

  .heading {
    padding: 0 4em 0 4em;
  }

  .chart-container {
    height: 500px;
  }

  .chart-inner {
    margin: 0 auto;
    overflow-y: hidden;
    width: 1200px;
  }

  .top-padding-large {
    padding-top: 10em;
  }

  .top-padding-small {
    padding-top: 2em;
  }

  .flex-container {
    margin-left: 0;
    width: 100%;
  }

  .flex-container button {
    margin-bottom: 0;
  }

  .company-name-container {
    margin-left: -10em;
  }

  .company-name-container > p.meta {
    margin-right: 1em;
    -webkit-order: 1;
            order: 1;
  }

  .company-name-container > h2 {
    -webkit-order: 2;
            order: 2;
  }

  .company-list{
    padding: 0;
  }

  .company-list > li {
    padding-bottom: 4em;
  }

  .middle-info {
    padding: 2em 0 0 0;
  }

  .middle-info,
  .company-list > li {
    width: 600px;
  }
}
